    padding: 15px 0px;
    z-index: 2000;
    background: #fff; }
  body .sticky-wrapper {
    padding: 40px 0 80px 0; }
  body .is-sticky {
    max-width: 920px; }
  body .grid {
    margin: 5px; }
  body .grid-sizer,
  body .grid_item {
    width: 20%;
    padding: 1% .5% .5% .5%; }
  body .gutter-sizer {
    width: 0%; }

