body {

  background-color: skyBlue;
  min-height:200vh;

  .wrap {
    padding: 80px;
  }
  .navbar {
    padding: 10px 0 10px 130px;
  }


    .mainform {
      border: 1px solid #ddd;
      border-radius: 4px;
      font-family: 'Roboto-Regular', sans-serif;
    font-size: 1.2rem;
    //color: $grey-header;
    box-shadow: 0 1px 3px 0px rgba(0, 0, 0, 0.08);
    padding: 15px 0px;
    z-index: 2000;
    background: #fff;
    }

  .sticky-wrapper {
    padding: 40px 0 80px 0;
  }

  .is-sticky {
    max-width: 920px;

    }

  .grid {
  margin: 5px;
  }
  /* ---- .grid_item ---- */

    .grid-sizer,
    .grid_item {
      width: 20%;
      padding: 1% .5% .5% .5%;

    }

  .gutter-sizer { width: 0%; }

}/* MORE THAN 75 Xl */
@media (max-width: 74.9em) { body {

  background-color: pink;
  .wrap {
    padding: 10px;
  }
  .navbar {
    padding: 0 0 0 60px;
  }
  .grid {
  margin: 5px;
  }
  /* ---- .grid_item ---- */

    .grid-sizer,
    .grid_item {
      width: 33%;
      padding: 1% .5% .5% .5%;

    }

  .gutter-sizer { width: 0%; }


} }/* LESS THAN 75 Lg */
