    }

  .gutter-sizer { width: 0%; }

}


}/* LESS THAN 62 Md */
@media (max-width: 47.9em) { body
  {
    background-color: green;
    .wrap {
      padding: 20px;
    }
    .mainform {
      border: 1px solid #ddd;
      border-radius: 4px;
      font-family: 'Roboto-Regular', sans-serif;
    font-size: 1.2rem;
    //color: $grey-header;
    margin: 20px;
    box-shadow: 0 1px 3px 0px rgba(0, 0, 0, 0.08);
    padding: 15px 0px;
    }
    /* ---- .grid_item ---- */

      .grid-sizer,
      .grid_item {
        width: 50%;
        padding: 2%;

      }

  }





}/* LESS THAN 48 Sm */
@media (max-width: 33.9em) {
  body {
  background-color: red;
  .wrap {
    padding: 0px;
  }
  .navbar {
    padding: 0 0 0 10px;
  }
}


}/* LESS THAN 34 Xs */

/* Navbar Styles */
.navbar{border-radius:0;}
.navbar-toggler{border:1px solid rgba(0,0,0,0.2);}
.navbar-toggler:hover{background:rgb(240,240,240)}
.navbar-toggler:focus{outline:0;}
.dropdown-toggle{cursor:pointer;}

/* Navbar Collapse Styles To Stack Items At Mobile Widths*/
@media(max-width:47.9em) {
  .navbar .collapse{max-height:250px;width:100%;overflow-y:auto;overflow-x:hidden;}
  .navbar .navbar-brand {float:none;display: inline-block;}
  .navbar .navbar-nav>.nav-item { float: none; margin-left: .1rem;}
  .navbar .navbar-nav {float:none !important;}
  .nav-item{width:100%;text-align:left;}
  .navbar .collapse .dropdown-menu {
    position: relative;
    float: none;
    background:none;
    border:none;
  }
}

